@import url(https://fonts.googleapis.com/css?family=sans-serif:300);
body {
  background: radial-gradient(
    circle,
    rgba(238, 174, 202, 1) 0%,
    rgba(165, 182, 203, 1) 100%
  );
  display: flex;
  min-height: 100vh;
  justify-content: center;
  align-items: center;
}
nav.navbar {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
}
.h3 {
  text-align: center;
}
.form-container {
  width: 400px;
  background-color: white;
  margin: auto;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
  padding: 10px;
}

.register-form {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  padding: 10px;
}

.form-field {
  margin: 10px 0 10px 0;
  padding: 15px;
  font-size: 16px;
  border: 0;
  font-family: "Roboto", sans-serif;
}

input {
  background: #f2f2f2;
}

.error {
  border-style: solid;
  border: 2px solid #ffa4a4;
}

button {
  background: #456c9b;
  color: white;
  cursor: pointer;
}

button:disabled {
  cursor: default;
}


